// src/store/modules/auth.js

export default {
    namespaced: true,
    state: {
        isLoggedIn: false,
        user:null,
        // 
    },
    mutations: {
        login(state,user) {
            state.isLoggedIn = true;
            state.user = user;
        },
        logout(state) {
            state.isLoggedIn = false;
            state.user = null;
        }
        //  mutations
    },
    actions: {
        login({ commit },user) {
            //  API 
            commit('login',user);
        },
        logout({ commit }) {
            //  API 
            commit('logout');
        }
        //  actions
    },
    getters: {
        isLoggedIn: state => state.isLoggedIn,
        user: state => state.user
        //  getters
    }
};
