<template>
        <div class="product-card">
            <router-link :to="{name: 'ProductDetail',params:{id:productId}}">
            <img :src="productImagerUrl" class="product-image" alt="Product Image" @error="imageError">
            <h5 class="card-title">{{ product.name }}</h5>
            <p class="card-text">{{ product.price }} </p>  
            </router-link>
            <button class="btn btn-primary" @click="AddToCart"></button>
        </div>
</template>
    
<script>
import { useStore } from 'vuex';
import { computed } from 'vue';

export default {
    name: 'ProductCard',
    props: {
        product: {
        type: Object,
        required: true,
        validator: function(obj) {
            return obj.id && obj.name && obj.price;
        }
        }
    },
    setup(props) {
        const store = useStore();

        // 
        const productId = computed(() => String(props.product.id).trim());
        const productImagerUrl = computed(() => `/img/product/${props.product.image}`);

        // 
        const addToCart = () => {
        const isLoggedIn = store.state.auth.isLoggedIn;
        
        if (!isLoggedIn) {
            store.dispatch('showLoginModal', true); //  action
            return;
        }

        store.dispatch('cart/addToCart', props.product);
        store.dispatch('showNotification', {
            type: 'success',
            message: ` ${props.product.name} `
        });
        };

        const imageError = (event) => {
        event.target.src = '/img/wrong.png'; // 
        };

        return {
        productId,
        productImagerUrl,
        addToCart,
        imageError
        };
    }
};
</script>

<style scoped>
.product-card {
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 8px;
    text-align: center;
}
.product-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-bottom: 10px;
}
.btn-primary {
    background-color: #007bff;
    border: none;
    color: white;
}
</style>
