<template>
    <div class="cart-container container mt-5">
        <h2 class="text-center mb-4"></h2>
        <div v-if="cartItems.length === 0" class="text-center">
            <p></p>
            <router-link to="/" class="btn btn-primary"></router-link>
        </div>
        <template v-else>
            <table class="table table-hover">
                <thead>
                    <tr>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                        <th scope="col"></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item, index) in cartItems" :key="item.id">
                        <!--  -->
                        <td>
                            <img :src="item.image" alt="item.name" class="product-image" @error="imageError"/>
                        </td>
                        <td>{{ item.name }}</td>
                        <td>{{ item.price }}</td>
                        <!-- input -->
                        <td>
                            <input type="number" class="form-control quantity-input" 
                            v-model.number="item.quantity" 
                            @input="updateQuantity(index,item.quantity)" min="1" />
                        </td>
                        <td>{{ item.quantity * item.price }}</td>
                        <td>
                            <button class="btn btn-danger" @click="removeItem(index)"></button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <div class="d-flex justify-content-between align-items-center mt-4">
                <h4>{{ cartTotal }}</h4>
                <button class="btn btn-success" @click="checkout"></button>
            </div>
        </template>
    </div>
</template>

<script>
import { useStore } from 'vuex';
import { computed } from 'vue';
import { useRouter } from 'vue-router';
import axios from 'axios';

export default {
    name: 'ShoppingCart',
    setup() {
        const store = useStore();
        const router = useRouter();

        // 
        const cartItems = computed(() => store.state.cart.items);
        const cartTotal = computed(() => store.getters['cart/cartTotal']);
        const isLoggedIn = computed(() => store.state.auth && store.state.auth.isLoggedIn);

        // 
        const updateQuantity = (index, quantity) => {
        if (quantity < 1) quantity = 1;
        store.dispatch('cart/updateItemQuantity', { index, quantity });
        };
        const imageError = (event)=>{
            event.target.src='img/wrong.png';
        }

        const removeItem = (index) => {
        store.dispatch('cart/removeItemFromCart', index);
        };

        const checkout = async () => {
        if (!isLoggedIn.value) {
            router.push({ name: 'Login', query: { redirect: '/cart' }});
            return;
        }

        if (cartItems.value.length === 0) {
            store.dispatch('showNotification', {
            type: 'warning',
            message: ''
            });
            return;
        }

        try {
            const response = await axios.post('/api/orders', {
            items: cartItems.value,
            total: cartTotal.value
            });
            
            store.dispatch('cart/clearShoppingCart');
            store.dispatch('showNotification', {
            type: 'success',
            message: ''
            });
            
            router.push({ 
            name: 'OrderConfirmation', 
            params: { orderId: response.data.orderId }
            });
        } catch (error) {
            console.error('', error);
            store.dispatch('showNotification', {
            type: 'error',
            message: ''
            });
        }
        };

        return {
        cartItems,
        cartTotal,
        updateQuantity,
        removeItem,
        checkout,
        imageError,
        };
    }
};
</script>

<style scoped>
.cart-container{
    max-width:800px;
    margin:0 auto;
}

.quantity-input{
width:80px;
text-align: center;
}
.table-hover today tr:hover{
    background: #f1f1f1;
}

.product-image{
    width:60px;
    height:60px;
    object-fit:cover;
}

.btn-danger{
    background-color: #dc3545;
}

.btn-success{
    background-color: #28a745;
}
.h4{
    font-weight:bold;
}

input[type="number"]{
    width:80px;
}


</style>
