<template>
<div class="container mt-5">
    <h2 class="mb-4"></h2>
    <!-- -->
        <div class="card mb-3">
            <div class="card-body">
            <h5 class="card-title"></h5>
            <p class="card-text">{{ userInfo.name }} </p>
            <p class="card-text">{{ userInfo.email }} </p>
            <p class="card-text">{{ userInfo.phone }} </p>
            <p class="card-text">: {{ userInfo.address }} </p>
            </div>
        </div>
    <!-- -->
    <div class="card mb-3">
        <div class="card-body">
            <h5></h5>
            <button class="btn btn-link" @click="toggleRecipient">/</button>
            <div v-if="showRecipient">
            <table class="table">
                <thead>
                    <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(recipient, index) in userInfo.recipients" :key="index">
                        <td>{{ recipient.name }}</td>
                        <td>{{ recipient.phone }}</td>
                        <td>{{ recipient.address }}</td>
                        <td>
                            <button class="btn btn-primary btn-sm" @click="editRecipient(index)"></button>
                            <button class="btn btn-danger btn-sm" @click="deleteRecipient(index)"></button>
                        </td>
                    </tr>
                </tbody>
            </table>
            <button class="btn btn-primary" @click="addRecipient"></button>
        </div>
    </div>
    <!--  -->
    <div class="mb-3">
        <div class="card-body">
            <h5 class="card-title"></h5>
            <table class="table">
                <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(order,index) in orders" :key="index">
                    <td>{{ order.id }}</td>
                    <td>{{ order.total }}</td>
                    <td>{{ order.date }}</td>
                    <td>
                        <button class="btn btn-link" @click="toggleOrderDetails(index)">/</button>
                    </td>
                </tr>
                <!--  -->
                <tr v-if="expandedOrder === index">
                    <td colspan="4">
                        <table class="table">
                            <tbody>
                                <tr v-for="(orderItem, i) in order.items" :key="i">
                                    <td colspan="4">
                                        <strong>:</strong>{{ orderItem.name }}
                                        <strong>:</strong> {{ orderItem.quantity }}
                                        <strong>:</strong> {{ orderItem.price }}
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
    <!--  -->
        <div class="text-end">
        <button class="btn btn-danger" @click="logout"></button>
        </div>
    </div>
    <!--  -->
    <div class="modal fade" id="recipientModal" tabindex="-1" aria-labelledby="recipientModalLabel" aria-hidden="true">
        <div class="mmodal-dialog">
        <div class="modal-content">
            <div class="modal-header">
                <h5 class="modal-title" id="recipientModalLabel">{{ isEditing ? '':'' }}</h5>
                <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
            </div>
            <div class="modal-body">
                <div class="mb-3">
                    <label for="recipientName" class="form-label"></label>
                    <input type="text" class="form-control" v-model="editRecipient.name" />
                </div>
                <div class="mb-3">
                    <label for="recipientPhone" class="form-label"></label>
                    <input type="text" class="form-control" v-model="editRecipient.phone" />
                </div>
                <div class="mb-3">
                    <label for="recipientAddress" class="form-label"></label>
                    <input type="text" class="form-control" v-model="editRecipient.address" />
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-bs-dismiss="modal"></button>
                <button type="button" class="btn btn-primary" @click="saveRecipient">{{ isEditing ? '':'' }}</button>   
        </div>
    </div>
</div>
</div>
</div>
</template>

<script>
import { Modal } from 'bootstrap';
import {mapState , mapActions} from 'vuex';
import axios from 'axios';

export default {
name: "MemberPage",
data() {
    return {
        showRecipient:false,
        expandedOrder:null,
        isEditing:false,
        editRecipient:{
            name: ' ',
            phone: ' ',
            address:' '  
        },
        editingIndex:-1,
        userInfo:{
            name:" ",
            email:" ",
            phone:'',
            address:'',
            recipients:[ ],
            },
        orders:[ ],
        };
    },
computed:{
    ...mapState({
        isLoggedIn:state => state.auth.isLoggedIn,
        user: state => state.auth.user
    })
},
mounted(){
    if(!this.isLoggedIn){
        alert('');
        this.$router.push('/UserLogin');
        return;
    }
        this.loadUserInfo();
        this.loadOrders();
    },
methods: {
    async loadUserInfo(){
        try{
            const userID = this.user.id;
            console.log(" ID:", userID); //  ID
            if(!userID){
                console.error("ID");  
                return;
            }
            const response = await axios.get(`/api/users/${userID}`); //API
            this.userInfo = response.data;
            console.log("",this.userInfo);
        }catch (error){
            console.error(":", error);
            console.error(':', error.response);
            console.error(':', error.config);
            alert("");
        }

    },
    async loadOrders(){
        try{
            const userID = this.userID;
            console.log("ID",userID); //
            if(!userID){
                console.error("ID");  
                return;
            }
            const response = await axios.get(`/api/orders`,{
                params: {
                    userId: userID
                }
            });
            this.orders = response.data;   
        }catch(error){
            console.error("",error);
            alert("");
        }
    },
    toggleRecipient(){
        this.showRecipient = !this.showRecipient;
    },
    addRecipient(){
        this.isEditing = false;
        this.editRecipient = {name: '',phone: '',address: '' };
        this.showModal();
    },
    editRecipientInfo(index){
        this.isEditing = true;
        this.editingIndex = index;
        this.editRecipient = {...this.userInfo.recipients[index]};
        this.showModal();
    },
    async saveRecipient(){
        if(this.isEditing){
            this.userInfo.recipients[this.editingIndex] = {...this.editRecipient };
        }else{
            this.userInfo.recipients.push({...this.editRecipient});
        }
        const userID = this.user.id;
        await axios.put(`/api/users/${userID}`,this.userInfo);
        this.hideModal();
    },
    toggleOrderDetails(index){
        this.expandedOrder = this.expandedOrder === index? null : index;
    },
    showModal() {
        const modal = new Modal(document.getElementById('recipientModal'));
        modal.show();
    },
    hideModal(){
        const modal = Modal.getInstance(document.getElementById('recipientModal'));
        modal.hide();
    },    
    ...mapActions('auth',['logout']),
    async logout(){
        await this.logout();
        alert("!");
        this.$router.push("/UserLogin"); // 
    }
    }
}
</script>

<style scoped>
    .card {
    margin-bottom: 1rem;
    }

    .search-box {
    margin-bottom: 1rem;
    }

    .table th,
    .table td {
    vertical-align: middle;
    }

    .modal-content {
    padding: 1rem;
    }
</style>