<template> 
    <div class="admin-dashboard container mt-5">
        <h2 class="text-center"></h2>
        <div class="filters d-flex justify-content-between mb-3">
            <!--  -->
            <input 
                type="text" 
                class="form-control me-2" 
                placeholder="" 
                v-model="searchQuery"
            />
            <select 
                v-model="selectedStatus" 
                class="form-select me-2" 
                aria-label=""
            >
                <option value=""></option>
                <option value="pending"></option>
                <option value="shipped"></option>
                <option value="completed"></option>
                <option value="cancelled"></option>
            </select>
            <button class="btn btn-primary" @click="filterOrders"></button>
        </div>
        <!--  -->
        <table class="table table-hover">
            <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>   
                </tr>
            </thead>
            <tbody>
                <tr v-for="order in paginatedOrders" :key="order.id">
                    <td>{{ order.id }}</td>
                    <td>{{ order.userId }}</td>
                    <td>{{ formatCurrency(order.total) }}</td>
                    <td>{{ formatStatus(order.status) }}</td>
                    <td>{{ formatDate(order.orderDate) }}</td>
                    <td>
                        <button class="btn btn-info btn-sm me-2" @click="viewOrderDetails(order)"></button>
                        <button 
                            class="btn btn-danger btn-sm" 
                            @click="cancelOrder(order)"
                            :disabled="order.status === 'cancelled' || order.status === 'completed'"
                            :title="order.status === 'cancelled' || order.status === 'completed' ? '' : ''"
                        >
                            
                        </button>
                    </td>
                </tr>
                <tr v-if="paginatedOrders.length === 0">
                    <td colspan="6" class="text-center"></td>
                </tr>
            </tbody>
        </table>
        <!--  -->
        <div class="d-flex justify-content-center align-items-center">
            <button class="btn btn-outline-primary me-2" @click="prevPage" :disabled="currentPage === 1"></button>
            <span>{{ currentPage }} / {{ totalPages }}</span>
            <button class="btn btn-outline-primary ms-2" @click="nextPage" :disabled="currentPage === totalPages"></button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import debounce from 'lodash.debounce';

export default {
    name: 'AdminOrders',
    data() {
        return {
            orders: [], 
            users:[],
            searchQuery: '',
            selectedStatus: '',
            currentPage: 1,
            itemsPerPage: 10,
            debouncedFilter: null, //  debouncedFilter
        };
    },
    computed: {
        filteredOrders() {
            return this.orders.filter(order => {
                const matchesSearch = (order.userId && order.userId.toString().toLowerCase().includes(this.searchQuery.toLowerCase())) ||
                                    (order.id && order.id.toString().toLowerCase().includes(this.searchQuery.toLowerCase()));
                const matchesStatus = this.selectedStatus ? order.status === this.selectedStatus : true;
                return matchesSearch && matchesStatus;
            });
        },
        totalPages() {
            return Math.ceil(this.filteredOrders.length / this.itemsPerPage) || 1;
        },
        paginatedOrders() {
            const start = (this.currentPage - 1) * this.itemsPerPage;
            const end = this.currentPage * this.itemsPerPage;
            return this.filteredOrders.slice(start, end);
        }
    },
    methods: {
        async fetchOrders() {
            try {
                const response = await axios.get('/api/orders');
                console.log('API',response.data);
                const ordersData = response.data.orders || response.data;
                this.orders = ordersData.map(
                        order=>({
                            ...order,
                            status:order.status || 'pending'
                        }))  || [] ;
            } catch (error) {
                console.error('', error);
                alert('');
                this.orders = [];
            }
        },
        async fetchUsers() {
            try{
                const response = await axios.get('/api/users');
                this.users = response.data.users || [];
            }catch(error){
                console.error('',error);
                alert('');
                this.users = [];
            }
        },
        viewOrderDetails(order) {
            console.log('', order);
            this.$router.push({ name: 'OrderDetail', params: { id: order.id } });
        },
        async cancelOrder(order) {
            if (confirm('')) {
                try {
                    await axios.put(`/api/orders/${order.id}/cancel`);
                    order.status = 'cancelled';
                    alert('');
                } catch (error) {
                    console.error('', error);
                    alert('');
                }
            }
        },
        prevPage() {
            if (this.currentPage > 1) {
                this.currentPage--;
            }
        },
        nextPage() {
            if (this.currentPage < this.totalPages) {
                this.currentPage++;
            }
        },
        formatCurrency(amount) {
            return new Intl.NumberFormat('zh-TW', { style: 'currency', currency: 'TWD' }).format(amount);
        },
        formatStatus(status) {
            const statusMap = {
                pending: '',
                shipped: '',
                completed: '',
                cancelled: ''
            };
            return statusMap[status] || '';
        },
        formatDate(dateStr) {
            const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
            return new Date(dateStr).toLocaleDateString('zh-TW', options);
        },
        filterOrders() {
            this.currentPage = 1;
        },
        getUserName(userId){
            const user = this.users.find(u=>u.id == userId);
            return user? user.name : '';
        },
    },
    mounted() {
        // API
        this.fetchOrders();
        this.fetchUsers();
        //  debouncedFilter  debounce(this.filterOrders, 300)
        this.debouncedFilter = debounce(this.filterOrders, 300);
        
    },
    watch: {
        searchQuery() {
            this.debouncedFilter();
        },
        selectedStatus() {
            this.debouncedFilter();
        }
    }
};
</script>

<style scoped>
.admin-dashboard {
    max-width: 1000px;
    margin: 0 auto;
}
.filters {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
}
.table-hover tbody tr:hover {
    background-color: #f1f1f1;
}
.btn-sm {
    padding: 0.25rem 0.5rem;
}
</style>
