<template>
<div class="container mt-5">
    <h2 class="text-center mb-4"></h2>
    <form @submit.prevent="createProduct">
    <div class="mb-3">
        <label for="productName" class="form-label"></label>
        <input type="text" v-model="productForm.name" class="form-control" required />
    </div>
    <div class="mb-3">
        <label for="description" class="form-label"></label>
        <textarea v-model="productForm.description" class="form-control" required></textarea>
    </div>
    <div class="mb-3">
        <label for="price" class="form-label"></label>
        <input type="number" v-model="productForm.price" class="form-control" required />
    </div>
    <div class="mb-3">
        <label for="stock" class="form-label"></label>
        <input type="number" v-model="productForm.stock" class="form-control" required />
    </div>
    <!--  -->
    <div class="mb-3">
        <label for="brand_id" class="form-label"></label>
            <multiselect
            v-model="productForm.brand_id"
            :options="brands"
            label="name"
            placeholder="">
            </multiselect>
            <span v-if="!productForm.brand_id && formSubmitted" class="text-danger"></span>
    </div>
    <div class="mb-3">
            <label for="tag_ids" class="form-label"></label>
            <multiselect 
                v-model="productForm.tag_ids" 
                :options="tags" 
                label="name"
                :multiple="true"
                placeholder=""
            ></multiselect>
            <span v-if="!productForm.tag_ids.length && formSubmitted" class="text-danger"></span>
    </div>
    <!--  -->
    <div class="mb-3">
        <label for="productImage" class="form-label"></label>
        <input type="file" class="form-control" id="productImage" @change="selectImage" accept="image/*"/>
    </div>
    <!--  -->
    <div v-if="productForm.imagePreview">
        <h5></h5>
        <img :src="productForm.imagePreview" alt="" style="max-width:200px;"/>
    </div>
    <button type="submit" class="btn btn-primary"></button>
    </form>
</div>
</template>

<script>
import axios from 'axios';
import Multiselect from 'vue-multiselect';
import 'vue-multiselect/dist/vue-multiselect.min.css';

export default {
name: 'CreateProduct',
components: {
    Multiselect,
},
data() {
    return {
    productForm: {
        name: '',
        description: '',
        price: 0,
        stock: 0,
        brand_id: null,
        tag_ids:[],
        created_at: '',
        imageFile: null,
        imagePreview: '',
    },
    brands:[],
    tags: [],
    formSubmitted: false,
    };
},
mounted() {
    this.loadBrands();
    this.loadTags();
},
methods: {
    //  
    async loadBrands() {
    try {
        const response = await axios.get('/api/brands');
        this.brands = response.data;
    } catch (error) {
        console.error(':', error);
    }
    },
    // 
    async loadTags() {
    try {
        const response = await axios.get('/api/tags');
        this.tags = response.data;
    } catch (error) {
        console.error(':', error);
    }
    },
    async createProduct() {
        this.formSubmitted = true; //   
        if(!this.productForm.brand_id || !this.productForm.tag_ids.length) {
            alert('');
            return;
        }

    try {
        //  created_at
        const currentDate = new Date().toISOString().split('T')[0]; // yyyy-mm-dd 
        this.productForm.created_at = currentDate;
        // FormData 
        const formData = new FormData();
        formData.append('name',this.productForm.name);
        formData.append('description',this.productForm.description);
        formData.append('price',this.productForm.price);
        formData.append('stock',this.productForm.stock);
        formData.append('brand_id', this.productForm.brand_id);
        formData.append('tag_ids', JSON.stringify(this.productForm.tag_ids.map(tag => tag.id || tag)));
        formData.append('created_at', this.productForm.created_at);
         // 
        if (this.productForm.imageFile) {
                formData.append('image', this.productForm.imageFile);
        }

        //  POST 
        const response = await axios.post('/api/products',formData, {
            headers:{
                'Content-Type':'multipart/form-data',
            },
        }); 
        console.log('',response.data);
        alert('');
        this.$router.push('/admin/products'); // 
    } catch (error) {
        console.error(':', error);
        alert('');
    }
    },
    // 
    selectImage(event){
        const file = event.target.files[0];
        if(file) {
            // public/img/public
            this.productForm.imageFile = file;
            // 
            this.productForm.image = URL.createObjectURL(file);
        }
    },

}
};
</script>

<style scoped>
.container {
max-width: 600px;
margin: 0 auto;
}
</style>
