<template>
    <div class="container mt-5">
        <h2 class="text-center"></h2>
        <form @submit.prevent="updateProduct">
        <div class="mb-3">
            <label for="productName" class="form-label"></label>
            <input
            type="text"
            id="productName"
            class="form-control"
            v-model="product.name"
            required
            />
        </div>
        <div class="mb-3">
            <label for="productPrice" class="form-label"></label>
            <input
            type="number"
            id="productPrice"
            class="form-control"
            v-model="product.price"
            required
            />
        </div>
        <div class="mb-3">
            <label for="productStock" class="form-label"></label>
            <input
            type="number"
            id="productStock"
            class="form-control"
            v-model="product.stock"
            required
            />
        </div>
        <div class="mb-3">
            <label for="productDescription" class="form-label"></label>
            <textarea
            id="productDescription"
            class="form-control"
            v-model="product.description"
            required
            ></textarea>
        </div>
        <div class="mb-3">
            <label for="productBrand" class="form-label"></label>
            <v-select
            :options="brands"
            label="name"
            placeholder=""
            :reduce="brand => brand.id"
            @input="updateBrandId"
            required
            >
            </v-select>
        </div>
        <div class="mb-3">
            <label for="productTags" class="form-label"></label>
            <v-select
            :options="tags"
            label="name"
            v-model="selectedTags"
            placeholder=""
            multiple
            :reduce="tag => tag.id"
            @input="updateTagIds"
            >

            </v-select>
        </div>
        <button type="submit" class="btn btn-primary"></button>
        </form>
    </div>
</template>
<script>
import axios from 'axios';
import vSelect from 'vue-select';
import 'vue-select/dist/vue-select.css';

export default{
    name:'EditProduct',
    components:{
        vSelect,
    },
    data(){
        return{
            product:{
                id: null,
                name: '',
                price: 0,
                stock: 0,
                description: '',
                brand_id:'',
                tag_ids: [],
            },
            brands: [],
            tags: [],
            selectedBrand:null,
            selectedTags:[],
        };
    },
    async mounted(){
        await Promise.all([
        this.loadProduct(),
        this.loadBrands(),
        this.loadTags(),
        ]);

    },
    methods:{
        async loadProduct(){
            const productId = this.$route.params.id;
            try{
                const response = await axios.get(`/api/products/${productId}`);
                this.product = {
                    ...response.data,
                    tag_ids:Array.isArray(response.data.tag_ids) ? response.data.tag_ids: []
                };

                this.$nextTick(()=>{
                    this.selectedBrand = this.brands.find(brand => brand.id === this.product.brand_id)||null;
                })
                this.selectedTags = this.tags.filter(tag => this.product.tag_ids && this.product.tag_ids.includes(tag.id));
            }catch(error){
                console.error('',error);
                alert('');
            }
        },
        async loadBrands(){
            try{
                const response = await axios.get('/api/brands');
                this.brands = response.data;

                if(this.product.brand_id){
                    this.selectedBrand = this.brands.find(brand => brand.id === this.product.brand_id) || null;
                }
            }catch(error){
                console.error('',error);
            }
        },
        async loadTags(){
            try{
                const response = await axios.get('/api/tags');
                this.tags = response.data;

                if(this.product.tag_ids.length > 0){
                    this.selectedTags = this.tags.filter(tag => this.product.tag_ids.includes(tag.id));
                }
            }catch(error){
                console.error('',error);
                alert('');
            }
        },
        async updateProduct(){
            try{
                await axios.put(`/api/products/${this.product.id}`,this.product);
                alert('');
                this.$router.push({name:'AdminProducts'});
            }catch(error){
                console.error('',error);
                alert('');
            }
        },
        updateBrandId(selectedBrand){
            this.product.brand_id = selectedBrand; 
        },
        updateTagIds(selectedTags){
            this.product.tag_ids = selectedTags;
        },
    },
    watch:{
        brand(newBrands){
            if(this.product.brand_id){
                this.selectedBrand = newBrands.find(brand => brand.id === this.product.brand_id) || null;
            }
        },
        tags(newTags){
            if (Array.isArray(this.product.tag_ids) && this.product.tag_ids.length >0) {
                this.selectedTags = newTags.filter(tag => this.product.tag_ids.includes(tag.id));
            }
        }
    },  
};
</script>
<style>
.container{
    max-width: 600px;
}
</style>
