<template>
    <div class="special-offers-dashboard container mt-5">
        <h2 class="text-center"></h2>
        <div class="d-flex justify-content-end mb-3">
            <button class="btn btn-success" @click="openCreateSpecialOfferModal"></button>
        </div>
        <!--  -->
        <div class="accordion" id="specialOffersAccordion">
            <div class="accordion-item">
                <h2 class="accordion-header" id="headingSpecialOffers">
                    <button 
                        class="accordion-button collapsed" 
                        type="button"
                        data-bs-toggle="collapse" 
                        data-bs-target="#collapseSpecialOffers" 
                        aria-expanded="true" 
                        aria-controls="collapseSpecialOffers"
                    >
                        
                    </button>
                </h2>            
                <div id="collapseSpecialOffers" class="accordion-collapse collapse show" aria-labelledby="headingSpecialOffers" data-bs-parent="#specialOffersAccordion">
                    <div class="accordion-body">
                        <table class="table">
                            <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th> ()</th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-for="(offer, index) in specialOffers" :key="offer.id">
                                    <td>{{ getProductName(offer.product_id) }}</td>
                                    <td>{{ offer.price }}</td>
                                    <td>{{ offer.countdown }}</td>
                                    <td>{{ formatDate(offer.created_at) }}</td>
                                    <td>
                                        <button class="btn btn-warning btn-sm me-2" @click="openEditSpecialOfferModal(index)"></button>
                                        <button class="btn btn-danger btn-sm" @click="deleteSpecialOffer(offer.id, index)"></button>
                                    </td>
                                </tr>
                            </tbody>
                        </table>    
                    </div>
                </div>    
            </div>
        </div>
    
        <!--  Modal -->
        <div class="modal fade" tabindex="-1" ref="specialOfferModal">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">{{ isEditing ? '' : '' }}</h5>
                        <button type="button" class="btn-close" @click="closeSpecialOfferModal"></button>
                    </div>
                    <div class="modal-body">
                        <div class="mb-3">
                            <label for="productSelect" class="form-label"></label>
                            <input  type="text" 
                                    id="productSearch"
                                    class="form-control" 
                                    v-model="productSearchKeyword"
                                    @input="debouncedSearchProducts" 
                                    required/>
                            <ul class="list-group mt-2" v-if="searchResults.length > 0">
                                <li 
                                    class="list-group-item list-group-item-action"
                                    v-for="product in searchResults"
                                    :key="product.id"
                                    @click="selectProduct(product)"
                                >
                                {{ product.name }}
                                </li>
                            </ul>
                            <div v-if="selectedProduct" class="mt-2">
                                <strong></strong>{{ selectedProduct.name }}
                                <button type="button" class="btn btn-sm btn-outline-danger ms-2" @click="clearSelectedProduct"></button>
                            </div>
                        </div>
                        <div class="mb-3">
                            <label for="priceInput" class="form-label"></label>
                            <input type="number" id="priceInput" v-model="specialOfferForm.price" class="form-control" placeholder="" required />
                        </div>
                        <div class="mb-3">
                            <label for="countdownInput" class="form-label"> ()</label>
                            <input type="number" id="countdownInput" v-model="specialOfferForm.countdown" class="form-control" placeholder="" required />
                        </div>
                        <div class="mb-3">
                            <label for="createdAtInput" class="form-label"></label>
                            <input type="date" id="createdAtInput" v-model="specialOfferForm.created_at" class="form-control" required />
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" @click="closeSpecialOfferModal"></button>
                        <button type="button" class="btn btn-primary" @click="saveSpecialOffer">{{ isEditing ? '' : '' }}</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import { Modal } from 'bootstrap';
import debounce from 'lodash.debounce';

export default {
    name: 'SpecialOffers',
    data() {
        return {
            specialOffers: [],
            // products: [],
            specialOfferForm: {
                id: null,
                product_id: '',
                price: 0,
                countdown: 0,
                created_at: ''
            },
            isEditing: false,
            currentIndex: null,
            productSearchKeyword: '',
            searchResults: [],
            selectedProduct: null
        };
    },
    mounted() {
        this.loadSpecialOffers();
        // this.loadProducts();
        this.debouncedSearchProducts = debounce(this.searchProducts,500);
    },
    methods: {
        // 
        async loadSpecialOffers() {
            try {
                const response = await axios.get('/api/special_offers');
                this.specialOffers = response.data;
            } catch (error) {
                console.error('', error);
                alert('');
            }
        },
        // 
        async searchProducts() {
            if(this.productSearchKeyword.trim() ===''){
                this.searchResults = [];
                return;
            }
            try{
                const response = await axios.get('/api/products/search',{
                    params:{keyword:this.productSearchKeyword }
                });
                this.searchResults = response.data;
            }catch(error){
                console.error('', error);
                alert('');
            }
        },
        // 
        selectProduct(product){
            this.selectedProduct = product;
            this.specialOfferForm.product_id = product.id;
            this.searchResults = [];
            this.productSearchKeyword = '';
        },
        // 
        clearSelectedProduct(){
            this.selectedProduct = null;
            this.specialOfferForm.product_id = '';
        },
        // 
        async loadProducts() {
            try {
                const response = await axios.get('/api/products');
                this.products = response.data;
            } catch (error) {
                console.error('', error);
                alert('');
            }
        },
        // 
        formatDate(dateStr) {
            const date = new Date(dateStr);
            return date.toLocaleDateString();
        },
        // //  ID
        // getProductName(productId) {
        //     const product = this.products.find(p => p.id === productId);
        //     return product ? product.name : '';
        // },
        //  Modal
        openCreateSpecialOfferModal() {
            this.isEditing = false;
            this.specialOfferForm = {
                id: null,
                product_id: '',
                price: 0,
                countdown: 0,
                created_at: ''
            };
            this.selectedProduct = null;
            this.productSearchKeyword = '';
            this.searchResults = [];
            this.$nextTick(() => {
                const modal = new Modal(this.$refs.specialOfferModal);
                modal.show();
            });
        },
        //  Modal
        async openEditSpecialOfferModal(index) {
            this.isEditing = true;
            this.currentIndex = index;
            const offer = { ...this.specialOffers[index] };
            this.specialOfferForm = offer;
            try{
                const response = await axios.get(`/api/products/${offer.product_id}`);
                this.selectedProduct = response.data;
            }catch{
                console.error('');
                alert('');
            }
            this.productSearchKeyword = '';
            this.searchResults = [];
            this.$nextTick(() => {
                const modal = new Modal(this.$refs.specialOfferModal);
                modal.show();
            });
        },
        // ID
        async getProductName(productId){
            if(this.selectedProduct && this.selectedProduct.id === productId){
                return this.selectedProduct.name;
            }            
            try{
                const response = await axios.get(`/api/products/${productId}`);
                return response.data.name;
            }catch(error){
                console.error('');
                alert('');
                return '';
            }
        },
        //  Modal
        closeSpecialOfferModal() {
            const modalInstance = Modal.getInstance(this.$refs.specialOfferModal);
            if (modalInstance) {
                modalInstance.hide();
            }
        },
        // 
        async saveSpecialOffer() {
            // 
            if (!this.specialOfferForm.product_id || !this.specialOfferForm.price || !this.specialOfferForm.countdown || !this.specialOfferForm.created_at) {
                alert('');
                return;
            }

            try {
                if (this.isEditing) {
                    // 
                    await axios.put(`/api/special_offers/${this.specialOfferForm.id}`, this.specialOfferForm);
                    this.specialOffers[this.currentIndex] = { ...this.specialOfferForm };
                    alert('');
                } else {
                    // 
                    const response = await axios.post('/api/special_offers', this.specialOfferForm);
                    this.specialOffers.push(response.data);
                    alert('');
                }
                this.closeSpecialOfferModal();
            } catch (error) {
                console.error('', error);
                alert('');
            }
        },
        // 
        async deleteSpecialOffer(offerId, index) {
            if (confirm('')) {
                try {
                    await axios.delete(`/api/special_offers/${offerId}`);
                    this.specialOffers.splice(index, 1);
                    alert('');
                } catch (error) {
                    console.error('', error);
                    alert('');
                }
            }
        }
    }
};
</script>

<style scoped>
.special-offers-dashboard {
    max-width: 1200px;
    margin: 0 auto;
}
.list-group{
    max-height: 200px;
    overflow-y:auto;
}
</style>
