<template>
    <div class="login-container container mt-5">
        <h2 class="text-center mb-4"></h2>
        <form @submit.prevent="handleLogin">
            <div class="mb-3">
                <label for="email" class="form-label">/</label>
                <input type="email" id="email" v-model="email" name="email" class="form-control" placeholder="EMAIL" required>
            </div>
            <div class="mb-3">
                <label for="password" class="form-label"></label>
                <input type="password" id="password" v-model="password" name="password" class="form-control" placeholder="" required>
            </div>
            <div class="d-flex justify-content-between mb-3">
                <router-link class="text-decoration-none" to="/ForgotPwd"></router-link>
                <router-link class="text-decoration-none" to="/UserRegister"></router-link>
            </div>
            <button type="submit" class="btn btn-primary w-100"></button>
        </form>
    </div>

</template>
    
<script>
    import axios from 'axios';    

    export default {
        name: 'UserLogin',
        data(){
            return{
                email:'',
                password:''
            };
        },
        methods:{
            async handleLogin(){
                try {
                    const response = await axios.get("api/users",{
                        params:{
                                email:this.email,
                                password:this.password
                            }
                        }
                    );
                    const users = response.data;
                    console.log(":",users);

                    if(users.length > 0){
                        const user = users[0];
                        console.log("",user);
                        alert('!' + user.name);

                        //
                        if (user.role === 'admin') {
                            localStorage.setItem('authToken', 'adminToken');
                            localStorage.setItem('role', 'admin');
                            localStorage.setItem('userID', user.id);  //  ID  localStorage
                            alert('');
                            this.$nextTick(()=>{
                                this.$router.push('/admin'); // 
                            });
                        } else if ( user.role === 'user') {
                            localStorage.setItem('authToken', 'userToken');
                            localStorage.setItem('role', 'user');
                            localStorage.setItem('userID', user.id);  //  ID  localStorage
                            alert('');
                            this.$nextTick(()=>{
                                this.$router.push('/MembersPage');// 
                            });
                        } else {
                            alert('');
                        }
                    }
                }catch (error){
                    console.error("",error);
                    alert("");
                }
        }
        }
}
    </script>
    
    <style scoped>
    .login-container{
        max-width: 400px;
        width:100%;
        margin: 0 auto;
    }
    .form-control{
        max-width:400px;
        width:100%;
        justify-content: center;
    }
    .text-decoration-none{
        color: crimson;
    }
    .text-decoration-none:hover{
        text-decoration: underline;
    }


</style>
    