<template>
        <div class="order-detail container mt-5">
            <h2 class="text-center"></h2>
                <div v-if="order">
                <!--  -->
                    <div class="order-info mb-4">
                    <p><strong></strong>{{ order.id }}</p>
                    <p><strong></strong>{{ userName }}</p>
                    <p><strong></strong>{{ formatDate(order.orderDate) }}</p>
                    <p><strong></strong>{{ formatCurrency(order.total) }}</p>
                    <!--  -->
                    <div class="mb-3">
                        <label for="orderStatus" class="form-label"><strong></strong></label>
                        <select id="orderStatus" v-model="order.status" class="form-select" @change="updateOrderStatus">
                        <option value="pending"></option>
                        <option value="shipped"></option>
                        <option value="completed"></option>
                        <option value="cancelled"></option>
                        </select>
                    </div>
                    </div>
                <!--  -->
                <h4></h4>
                <table class="table table-bordered">
                    <thead>
                        <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="item in order.items" :key="item.productId">
                        <td>{{ item.productName }}</td>
                        <td>{{ item.quantity }}</td>
                        <td>{{ formatCurrency(item.price) }}</td>
                        <td>{{ formatCurrency(item.price * item.quantity) }}</td>
                        </tr>
                    </tbody>
                </table>
                <!--  -->
                <button class="btn btn-secondary mt-3" @click="goBack"></button>
            </div>
            <div v-else>
                <p>...</p>
            </div>
        </div>
</template>
    
<script>
    import axios from 'axios';
    
    export default {
        name: 'OrderDetail',
        props: ['id'],
        data() {
        return {
            order: null,
            userName: '',
        };
        },
        methods: {
            async fetchOrderDetails(orderId) {
                try {
                    const response = await axios.get(`/api/orders/${orderId}`);
                    this.order = response.data.order || response.data;
                    // 
                    this.fetchUserName(this.order.userId);
                } catch (error) {
                    console.error('', error);
                    alert('');
                    this.order = null;
                }
            },
            async fetchUserName(userId) {
                try {
                    const response = await axios.get(`/api/users/${userId}`);
                    const user = response.data.user || response.data;
                    this.userName = user.name || '';
                } catch (error) {
                    console.error('', error);
                    this.userName = '';
                }
            },
            formatCurrency(amount) {
                return new Intl.NumberFormat('zh-TW', { style: 'currency', currency: 'TWD' }).format(amount);
            },
            formatDate(dateStr) {
                const options = { year: 'numeric', month: '2-digit', day: '2-digit' };
                return new Date(dateStr).toLocaleDateString('zh-TW', options);
            },
            async updateOrderStatus() {
                if (!this.order) return;
                    try {
                        await axios.put(`/api/orders/${this.order.id}`,this.order);                   
                        alert('');
                    } catch (error) {
                        console.error('', error);
                        alert('');
                    }
                },
            goBack() {
                this.$router.go(-1);
            },
        },
        mounted() {
            const orderId = this.id || this.$route.params.id;
            this.fetchOrderDetails(orderId);
        },
    };
</script>
    
<style scoped>
    .order-detail {
        max-width: 800px;
        margin: 0 auto;
    }
    .order-info p {
        margin-bottom: 0.5rem;
    }
    .table-bordered {
        margin-top: 1rem;
    }
</style>
    