<template>
    <div class="carousel-management container mt-5">
        <h2 class="text-center"></h2>
        <div class="carousel-image-list d-flex flex-wrap">
            <div v-for="image in carouselImages" 
                :key="image.id" 
                class="carousel-image-item m-2"
                >
                <img :src="image.url" alt="" class="img-thumbnail">
                <button class="btn btn-danger btn-sm mt-2" 
                @click="deleteCarouselImage(image.id)"
                ></button>
                <div class="me-2">
                    <button 
                    class="btn btn-secondary btn-sm" 
                    @click="toggleImageVisibility(image)"
                    >
                    {{ image.visible ? '' : '' }}
                </button>
                </div>

            </div>
        </div>
            <!--  -->
        <div class="upload-section mt-4">
            <h4>
                
            </h4>
            <input type="file" @change="handleFileSelect" accept="image/*" ref="fileInput"/>
            <!--  -->
            <div v-if="previewUrl" class="preview-section mt-3">
                <h5></h5>
                <img :src="previewUrl" alt="" class="img-thumbnail" style="max-width:300px;" />
                <div class="mt-2">
                    <button class="btn btn-primary me-2" @click="confirmUpload"></button>
                    <button class="btn btn-secondary" @click="cancelUpload"></button>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
import axios from 'axios';

export default {
    name: 'AdminCarouselImages',
    data() {
        return {
            carouselImages: [],
            selectedFile:null,
            previewUrl:'',
        };
    },
    async mounted() {
        await this.loadCarouselImages();
    },
    methods: {
        async loadCarouselImages() {
            try {
                const response = await axios.get('/api/carousel_images');
                this.carouselImages = response.data || [];
            } catch (error) {
                console.error('', error);
                alert('');
            }
        },
        handleFileSelect(event){
            const file= event.target.files[0];
            if(file){
                const validTypes = ['image/jpeg', 'image/png', 'image/gif'];
                const maxSize = 5 * 1024 * 1024; // 5MB
                if(!validTypes.includes(file.type)){
                    alert('JPEG, PNG, GIF ');
                    return;
                }
                if(file.size > maxSize){
                    alert('5MB');
                    return;
                }
                this.selectedFile = file;
                this.previewUrl = URL.createObjectURL(file);
            }
        },
        async confirmUpload() {
            if(!this.selectedFile){
                alert('');
                return;
            }
            const formData = new FormData();
            formData.append('image',this.selectedFile);

            try {
                const response = await axios.post('/api/carousel-images', formData, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    },
                });
                this.carouselImages.push(response.data);
                alert('');
                this.resetUpload();
            } catch (error) {
                console.error('', error);
                alert('!');
            }
        },
        cancelUpload(){
            this.resetUpload();
        },
        resetUpload(){
            this.selectedFile = null;
            this.previewUrl = '';
            this.$refs.fileInput.value = '';
        },
        async deleteCarouselImage(imageId) {
            if (confirm('')) {
                try {
                    await axios.delete(`/api/carousel-image/${imageId}`);
                    this.carouselImages = this.carouselImages.filter(image => image.id !== imageId);
                    alert('');
                } catch (error) {
                    console.error('', error);
                    alert('');
                }
            }
        },
        async toggleImageVisibility(image){
            const updatedVisibility =!image.visible;
            try{
                await axios.patch(`/api/carousel-image/${image.id}`,{
                    visible: updatedVisibility,
                });
                image.visible = updatedVisibility;
                alert(`{updatedVisibility ? '' : ''}`);
            }catch (error){
                console.error('', error);
                alert('');
            }

        }
    },
};
</script>

<style scoped>
.carousel-image-list {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
}

.carousel-image-item {
    width: 200px;
    text-align: center;
}

.preview-section img {
    max-width: 100%;
    height: auto;
}
</style>
