<template>
    <div class="container register-container mt-5 whitespace:nowrap">
        <h2 class="mb-4"></h2>
        <form @submit.prevent="handleRegister">
        <div class="mb-3 d-flex align-items-center">
            <label for="username" class="form-label me-2"></label>
            <input type="text" class="form-control" id="username" v-model="username" placeholder="" required />
        </div>
        <div class="mb-3 d-flex align-items-center">
            <label for="email" class="form-label me-2"></label>
            <input type="email" class="form-control" id="email" v-model="email" placeholder="" required />
        </div>
        <div class="mb-3 d-flex align-items-center">
            <label for="phone" class="form-label me-2"></label>
            <input type="tel" class="form-control" id="phone" v-model="phone" placeholder="" required />
        </div>
        <div class="mb-3 d-flex align-items-center">
            <label for="address" class="form-label me-2"></label>
            <input type="text" class="form-control" id="address" v-model="address"  placeholder="" required />
        </div>
        <div class="mb-3 d-flex align-items-center">
            <label for="password" class="form-label me-2"></label>
            <input type="password" class="form-control" id="password" v-model="password" placeholder="" />
        </div>
        <div class="mb-3 d-flex align-items-center">
            <label for="confirmpassword" class="form-label me-2"></label>
            <input type="password" class="form-control" id="confirmpassword" v-model="confirmpassword"  placeholder="" required />
            <div v-if="password!== confirmpassword" class="text-danger"></div>
        </div>
        <button type="submit" class="btn btn-primary"></button>
        </form>
    </div>
</template>

<script>
import axios from 'axios';

    export default {
        name: "UserRegister",
        data() {
            return {
            username: "",
            phone: "",
            address:"",
            email: "",
            password: "",
            confirmpassword: "" ,
            };
    },
    methods: {
        handleRegister() {
            // 
        if (!this.validatePhoneNumber()){
            alert('');
            return;
            }
        // 
        if (this.password !== this.confirmpassword){
            alert('');
            return;
            }
        //postAPI
            axios
            .post('/api/users',{
                name:this.username,
                phone:this.phone,
                address:this.address,
                email:this.email,
                password:this.password,
                role:"user",
            })
            .then(()=>{
                alert('');
                this.$router.push("/UserLogin"); // 

            })
            .catch(error=>{
                if (error.response) {
                    // 
                    console.error(':', error.response.data);
                    alert('');
                } else if (error.request) {
                    // 
                    console.error(':', error.request);
                    alert('');
                } else {
                    // 
                    console.error(':', error.message);
                    alert('');
                }
            });
        },
            validatePhoneNumber(){
            const phoneRegex = /^[0-9]{10}$/; //10
            return phoneRegex.test(this.phone);
        },
    },
}
</script>

<style scoped>
        .register-container{
        max-width: 500px;
        width:100%;
        margin: 0 auto;
    }
    .form-label{
        width: 200px;
        color: #3e3f4c;
        margin-bottom: 0;
}
</style>
